<template>
  <div>
    <div
      v-if="hasPermissions == true"
      style="width: 100%; height: calc(100% - 50px)"
    >
      <div class="interface-head"></div>
      <div class="page-box">
        <div class="clear search-condition-box">
          <!-- <div class="search-condition-box-item">
                        <a-month-picker
                            @change="timeChange"
                            v-model="searchList.statisticsTime"
                            value="searchList.statisticsTime"
                            placeholder="" 
                        />
                    </div> -->
          <div class="search-condition-box-item" style="width: 140px">
            <a-select
              v-model="searchList.groupName"
              style="width: 130px; display: inline-block; margin-left: 0px"
              allowClear
              placeholder=""
              @change="mealChange"
            >
              <a-select-option
                v-for="(item, index) in mealList"
                :key="index"
                :value="item.productKey"
                :title="item.productName"
                >{{ item.productName }} </a-select-option
              >
            </a-select>
          </div>

          <div class="search-condition-box-item" style="width: auto">
            <!-- <a-button @click="screenClear"></a-button> -->
            <a-button type="primary" @click="screenBtn"></a-button>
            <a-button @click="screenBtn_cancl"></a-button>
          </div>
        </div>

        <div class="package-price">
          <div class="package-price-line clear">
            (+)
            <span
              >{{ datalist.registerCustomerTotal }} ({{
                datalist.kuaifuRegisterTotal
              }}+{{ datalist.facilitatorRegister }})</span
            > 
            <span>{{ datalist.openCustomerTotal }}</span>
            
            <span>{{ datalist.openProportionTotal }}</span>
          </div>
        </div>

        <div class="form-box">
          <a-spin :spinning="table_spinning" tip="...">
            <a-table
              :columns="columns"
              :dataSource="data"
              bordered
              :pagination="false"
            >
              <template slot="registerCustomer" slot-scope="text">
                {{ text.registerCustomer }} ({{ text.kuaifuRegister }}+{{
                  text.facilitatorRegister
                }})
              </template>
            </a-table>
          </a-spin>
        </div>
      </div>
    </div>

    <div v-else class="no-permission" style="height: calc(100% - 50px)">
      <span></span>
    </div>
  </div>
</template>
<script>
import * as getData from "../api/api";
import moment from "moment";
export default {
  data() {
    return {
      hasPermissions: false,
      searchList: {
        groupName: "",
      },
      mealList: [],
      datalist: {
        registerCustomerTotal: 1000,
        openCustomerTotal: 765,
        openProportionTotal: "76.5%",
        kuaifuRegisterTotal: "",
        facilitatorRegister: "",
      },
      columns: [
        {
          title: "",
          dataIndex: "date",
          key: "1",
        },
        {
          title: "(+)",
          //dataIndex: "registerCustomer",
          scopedSlots: { customRender: "registerCustomer" },
          align: "right",
          key: "2",
        },
        {
          title: "",
          dataIndex: "openCustomer",
          align: "right",
          key: "3",
        },
        {
          title: "",
          dataIndex: "openProportion",
          align: "right",
          key: "4",
        },
      ],
      table_spinning: false,
      data: [],
    };
  },
  methods: {
    screenBtn_cancl() {
      console.log("123456");
      this.searchList.groupName = parseInt(this.timechangestart(new Date()));
      var data = {
        statisticsTime: this.searchList.groupName,
      };
      this.getdata(data);
    },
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w += i.width;
      }
      return w;
    },
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 330;
    },
    //
    getdata(params) {
      this.table_spinning = true;
      getData.getaccount(params).then((res) => {
        //console.log(res)
        //this.table_spinning = false
        if (res.code == 200) {
          this.table_spinning = false;
          res.data.openProportionTotal =
            (res.data.openProportionTotal * 100).toFixed(2) + "%";
          res.data.list.forEach((item) => {
            item.openProportion = (item.openProportion * 100).toFixed(2) + "%";
          });
          this.datalist = res.data;
          this.data = res.data.list;
        } else {
          this.table_spinning = false;
          this.datalist = [];
          this.data = [];
        }
      });
    },
    //
    changemealist() {
      this.mealList = [];
      var str = parseInt(this.timechangestart(new Date()));
      for (var i = 2016; i <= str; i++) {
        this.mealList.unshift({
          productKey: i,
          productName: i,
        });
      }
    },
    //
    mealChange() {
      //console.log(this.searchList.groupName)
    },
    timeChange(e) {
      // console.log(e)
      // // this.searchList.statisticsTime = this.timechangestart(e._d).toString
      // console.log(e._d.getFullYear())
    },
    //
    screenClear() {},
    //
    screenBtn() {
      var data = {
        statisticsTime: this.searchList.groupName,
      };
      this.getdata(data);
      // 
      //  
      // 
    },
    //
    timechangestart(str) {
      return str.getFullYear();
    },
  },
  mounted() {
    this.searchList.groupName = parseInt(this.timechangestart(new Date()));
    var data = {
      statisticsTime: this.searchList.groupName,
    };
    this.getdata(data);
    this.changemealist();
    this.hasPermissions = this.$store.state.pagepermission;
  },
};
</script>


