<template>
  <div class="aside">
    <div class="aside-head">
      <div></div>
    </div>
    <!-- <div
      class="aside-head-item"
      v-for="item in asideHead"
      :key="item.name"
      :class="{
        'aside-head-item-active':
          currentactive == item.route || currentactive == item.routemore
      }"
      @click="$router.push({ name: item.route })"
    >
      {{ item.name }}
    </div> -->
    <div>
      <a-menu
        mode="inline"
        :open-keys="openKeys"
        style="width: 100%; background-color: #f7f8fa"
        :default-selected-keys="selekeys"
        @openChange="onOpenChange"
        @select="titlechange"
      >
        <a-sub-menu key="sub1">
          <span slot="title"
            ><a-icon type="usergroup-add" /><span></span></span
          >
          <a-menu-item key="Manualinspection" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="Keywords" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="industryManage" title="">
            <span></span>
          </a-menu-item>
        </a-sub-menu>
        <a-sub-menu key="sub2">
          <span slot="title"
            ><a-icon type="appstore" /><span></span></span
          >
          <a-menu-item key="activeUser" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="regUser" title="">
            <span></span>
          </a-menu-item>
        </a-sub-menu>
        <a-sub-menu key="sub4">
          <span slot="title"
            ><a-icon type="area-chart" /><span></span></span
          >
          <a-menu-item key="meal_MonthReport" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="packageMonthReport" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="thirdMonthReport" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="Queueanalysis" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="Openaccount" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="Detailedmrr" title=" MRR">
            <span> MRR</span>
          </a-menu-item>
          <a-menu-item key="Channel" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="Newview" title="">
            <span></span>
          </a-menu-item>
          <a-menu-item key="Statistics" title="">
            <span></span>
          </a-menu-item>
          <!-- <a-menu-item key="Industry_QApool" title="">
            <span></span>
          </a-menu-item> -->
        </a-sub-menu>
        <a-menu-item key="PermissionSetting" title="">
          <a-icon type="setting" />
          <span></span>
        </a-menu-item>
      </a-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: "myaside",
  data() {
    return {
      selekeys: ["activeUser"],
      rootSubmenuKeys: ["sub1", "sub2", "sub4"],
      openKeys: ["sub1"],

      asideHead: [
        {
          name: "",
          route: "Manualinspection",
          routemore: "Inspectionresults",
        },
        {
          name: "",
          route: "regUser",
          routemore: "regUser",
        },
        {
          name: "",
          route: "activeUser",
          routemore: "activeUser",
        },
        {
          name: "",
          route: "meal_MonthReport",
          routemore: "meal_MonthReport",
        },
        {
          name: "",
          route: "packageMonthReport",
          routemore: "packageYearReport",
        },
        {
          name: "",
          route: "Queueanalysis",
          routemore: "Queueanalysis",
        },
        {
          name: "",
          route: "Openaccount",
          routemore: "Openaccount",
        },
        {
          name: "MRR",
          route: "Detailedmrr",
          routemore: "Detailedmrr",
        },
        {
          name: "",
          route: "Channel",
          routemore: "Channel",
        },
        {
          name: "",
          route: "thirdMonthReport",
          routemore: "thirdMonthReport",
        },
        {
          name: "",
          route: "Statistics",
          routemore: "Statistics",
        },
        // {
        //   name: "",
        //   route: "IndustryTargetCount",
        //   routemore: "IndustryTargetCount"
        // },
        {
          name: "",
          route: "Keywords",
          routemore: "Keywords",
        },

        {
          name: "",
          route: "Industry_QApool",
          routemore: "Industry_QApool",
        },
        {
          name: "",
          route: "PermissionSetting",
          routemore: "PermissionSetting",
        },
        {
          name: "",
          route: "Newview",
          routemore: "Newview",
        },
      ],
      currentactive: "regUser",
    };
  },
  created() {
    //console.log(window.location);
    //this.updateactive();
    var arrt = window.location.hash.indexOf("?");
    //console.log(arrt);
    if (arrt == -1) {
      this.selekeys[0] = window.location.hash.slice(2);
    } else {
      this.selekeys[0] = window.location.hash.slice(2, arrt);
    }

    //console.log(this.selekeys);
    this.changeselekys();
  },
  methods: {
    updateactive() {
      this.currentactive = this.$route.name;
    },
    onOpenChange(openKeys) {
      console.log(openKeys);
      const latestOpenKey = openKeys.find(
        (key) => this.openKeys.indexOf(key) === -1
      );
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys;
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    },
    titlechange(obj) {
      console.log(obj);
      this.$router.push({ name: obj.key });
      this.selekeys[0] = obj.key;
    },
    changeselekys() {
      if (
        this.selekeys[0] == "Manualinspection" ||
        this.selekeys[0] == "Keywords" ||
        this.selekeys[0] == "industryManage"
      ) {
        this.openKeys[0] = "sub1";
      } else if (
        this.selekeys[0] == "activeUser" ||
        this.selekeys[0] == "regUser"
      ) {
        this.openKeys[0] = "sub2";
      } else {
        this.openKeys[0] = "sub4";
      }
    },
  },
  watch: {
    // 
    //$route: "changeselekys"
  },
};
</script>

<style scoped>
.aside {
  position: relative;
  z-index: 1;
  margin-left: 60px;
}
</style>



